<template>
  <section class="home">
    <header class="navbar">
      <van-nav-bar>
        <template #left>
          <van-image
            width="78"
            height="18"
            src="data:image/png;base64,"
          />
        </template>
        <template #title>
          <button
            :class="{ active: gender === 'male' }"
            @click="$router.push('/?gender=male')"
          >
            男生
          </button>
          <button
            :class="{ active: gender === 'female' }"
            @click="$router.push('/?gender=female')"
          >
            女生
          </button>
        </template>
        <template #right>
          <van-image
            width="24"
            height="24"
            src="data:image/png;base64,"
          />
          <van-image
            width="24"
            height="24"
            src="data:image/png;base64,"
          />
        </template>
      </van-nav-bar>
      <div class="search">
        <div class="search-box">
          <van-swipe
            style="height: 30px"
            vertical
            :loop="true"
            :autoplay="3000"
            :show-indicators="false"
          >
            <van-swipe-item>重生千金：帝少的燃情宠妻</van-swipe-item>
            <van-swipe-item>鸿天神尊</van-swipe-item>
            <van-swipe-item>他从地狱里来</van-swipe-item>
            <van-swipe-item>娇娇女被九叔宠野了</van-swipe-item>
            <van-swipe-item>出名太快怎么办</van-swipe-item>
          </van-swipe>
          <van-image
            width="24"
            height="24"
            src="data:image/png;base64,"
          />
        </div>
      </div>
    </header>
    <div class="banner">
      <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
        <van-swipe-item v-for="(item, index) in bannersMale" :key="index">
          <van-image width="100vw" height="40vw" :src="item.image"></van-image>
        </van-swipe-item>
      </van-swipe>
      <van-grid :border="false">
        <van-grid-item v-for="(item,index) in bannerNavs" :key="index" :text="item.text" @click.native="item.path && $router.push(item.path)">
         <van-image  slot="icon" width="10vw"  :src="item.icon"></van-image>
        </van-grid-item>
      </van-grid>
      <van-divider :style="{ margin: 0 }"/>
      <div class="lately-read">
         <span>最近阅读</span>
      </div>
      <template v-if="gender=='male'">
        <HomeList v-for="item in maleList"
        :key="item.id"
        :list="item.books.slice(0.5)"
        >
          男生{{item.shortTitle}}</HomeList>
      </template>
      <template v-else>
        <HomeList v-for="item in femaleList"
        :key="item.id"
        :list="item.books.slice(0.5)"
        >
          女生{{item.shortTitle}}</HomeList>
      </template>
    </div>
  </section>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
import HomeList from "@/components/HomeList.vue";
import { mapMutations, mapGetters, mapState } from "vuex";
export default {
  name: "Home",
  components: {
    // HelloWorld
    HomeList
  },
  data: function () {
    return {
      gender: "male",
      bannersMale: [
        {
          image:
            "https://plf-new.zhuishushenqi.com/management/images/20210209/d55ce1e6f7584005820dff309b388917.png",
        },
        {
          image:
            "https://plf-new.zhuishushenqi.com/management/images/20201218/12ddcc817cdf49f787959bb53e21b584.png",
        },
        {
          image:
            "https://plf-new.zhuishushenqi.com/management/images/20210209/d57b17f1f3f7478ba1879fb631005e1a.jpg",
        },
      ],
      bannerNavs:[
         {
          icon:
            "data:image/png;base64,",
          text: "书架",
        },
        {
          icon:
            "data:image/png;base64,",
          text: "排行",
          path: "/ranking",
        },
        {
          icon:
            "data:image/png;base64,",
          text: "VIP",
        },
        {
          icon:
            "data:image/png;base64,",
          text: "充值",
        },
      ]
    };
  },
  computed:{
    ...mapState(["rankCategory", "maleList", "femaleList"]),
     ...mapGetters(["maleListIds", "femaleListIds"]),
  },
  methods:{
    ...mapMutations([
      "updateRankCategory",
      "updateMaleList",
      "updateFemaleList",
    ]),
    getListByGender:function(){
      if(this.gender==="female"){
        !this.femaleList &&
        Promise.all(
          this.femaleListIds.map((item)=>{
            return this.axios.get("https://novel.kele8.cn/rank/" + item._id);
          })
        ).then((res)=>{
          this.updateFemaleList(res.map((r)=>r.data.ranking))
        })
      }else{
         !this.maleList &&
          Promise.all(
            this.maleListIds.map((item) => {
              return this.axios.get("https://novel.kele8.cn/rank/" + item._id);
            })
          ).then((res) => this.updateMaleList(res.map((r) => r.data.ranking)));
      }
    }
  },
  created(){
     this.gender = this.$route.query.gender || "male";
       // 先判断排行分类 是否存在
    if (!this.rankCategory) {
      // 获取排行榜分类
      this.axios.get("https://novel.kele8.cn/rank-category").then((res) => {
        console.log(res);
        this.updateRankCategory(res.data);

        // 获取 排行分类成功
        this.getListByGender();
      });
    } else {
      this.getListByGender();
    }
    this.axios
    .get("https://novel.kele8.cn/recommend/53115e30173bfacb4904897e")
  .then((res) => {
    console.log(res);
  });
  },
  beforeRouteUpdate(to, from, next) {
    this.gender = to.query.gender || "male";
    this.getListByGender();
    next();
  },
};
</script>
