<template>
 <li class="book-item">
   <van-image
      width="70px"
      height="90px"
      fit="cover"
      :src="item.cover | parseImageUrl"
    />
    <div class="info">
   <h5 class="name">
     <span class="title">{{item.title}}</span>
     <!-- <i class="complete">完结</i> -->
   </h5>
   <p class="desc">{{item.shortIntro}}</p>
   <div class="category">
     <div>
     <span>未来世界</span>
     <span class="split">|</span>
     <span class="c-red">{{item.latelyFollower | sentIment}}人气</span>
     </div>
     <i class="tag">{{item.majorCate}}</i>
   </div>
    <van-divider :style="{ borderColor: '#c4c4c4', margin: '5px 0' }" />
 </div>
 </li>
 
</template>

<script>
export default {
  props: ["item"],
  filters: {
    parseImageUrl: function (str) {
      return "//statics.zhuishushenqi.com" + str;
    },
    sentIment:function(n){
      return n>=10000
      ?parseFloat((n / 10000).toFixed(1)) + "万"
        :n;
    }
  },
}
</script>

<style lang="less">
  li.book-item {
  display: flex;
  .van-image {
    vertical-align: middle;
  }
  .info {
    flex: 1;
    margin-left: 10px;

    line-height: 20px;
    font-size: 12px;
    h5.name {
      .title{
        line-height: 20px;
      font-size: 14px;
      }
      // .complete{
      //   background-color: green;
      //   display: inline-block;
      //   margin-left: 10px;
      //   border-radius: 2px;
      //   font-style: normal;
      //   color: #fff;
      //   font-size: 10px;
      //   padding: 1px 2px;
      // }
    }
    .desc {
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;

      margin: 5px 0;
    }
    .category {
      display: flex;
      justify-content: space-between;
      align-items: center;
      span {
        color: #666;
        &.split {
          margin: 0 5px;
        }
        &.c-red {
          color: #b83320;
        }
      }
      i.tag {
        font-size: 10px;
        font-style: normal;
        border: 1px solid #ff8400;
        // float: right;
        line-height: 1.2em;
        padding: 0 3px;
        border-radius: 3px;
        color: #ff8400;
      }
    }
  }
}
</style>